import React from "react"
import { useStaticQuery, graphql, Link } from "gatsby"
import icon from "../images/alpha.png"
import "../components/base.css"
import SocialLinks from "../components/sociallinks"
import Tags from "../components/tags"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import Footer from "../components/footer"
import PostLink from "../components/postlink"

function Header() {
  return (
    <header
      style={{
        width: "100%",
        height: "4rem",
      }}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Link to="/">
          <img
            src={icon}
            alt=""
            style={{
              height: "2.5rem",
              margin: "0.75rem 0",
            }}
          ></img>
        </Link>
        <SocialLinks color={"black"} marginLeft={"3rem"} fontWeight={"500"} />
      </div>
    </header>
  )
}

function ProjectLink({ url }) {
  return (
    <div>
      {url && (
        <a
          target="_blank"
          rel="noopener noreferrer"
          href={url}
          style={{
            color: "#1C2635",
            textDecoration: "underline",
            fontWeight: "500",
          }}
        >
          Project Link
        </a>
      )}
    </div>
  )
}

function PostContent({ node }) {
  const { title, images, description, date, videos, url, tag } = node

  return (
    <div
      style={{
        backgroundColor: "white",
        borderRadius: "0.3rem",
        padding: "2rem",
      }}
    >
      <p>{date}</p>
      <h2>{title}</h2>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "flex-end",
          marginBottom: "1rem",
        }}
      >
        <ProjectLink url={url} />
      </div>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "flex-end",
        }}
      >
        <Tags tags={tag} />
      </div>

      <div style={{ height: "1rem" }}></div>
      <p className="description">{description.description}</p>
      <div style={{ height: "1rem" }}></div>

      {images &&
        images.map(image => {
          return (
            <GatsbyImage
              className="thumb"
              placeholder="blurred"
              image={getImage(image)}
              style={{
                borderBottom: "solid #aaa 1px",
                marginBottom: "1rem",
              }}
              alt=""
            />
          )
        })}

      {videos != null &&
        videos.map(video => {
          return (
            <div style={{ width: "100%", textAlign: "center" }}>
              <video
                src={video.file.url}
                controls
                autoplay
                loop
                muted
                playsinline
                style={{
                  width: "100%",
                  //   width: "80%",
                  //   margin: "auto",
                  //   border: "solid 2px #ddd",
                }}
              ></video>
            </div>
          )
        })}
    </div>
  )
}

function Nav({ nodes, prevPostId, nextPostId }) {
  const prevNode = nodes.find(n => n.contentful_id === prevPostId)
  const nextNode = nodes.find(n => n.contentful_id === nextPostId)

  return (
    <ul
      style={{
        display: "flex",
        flexWrap: "wrap",
        justifyContent: "space-between",
        listStyle: "none",
        padding: "1rem",
        textDecoration: "underline",
      }}
    >
      <li>
        {prevPostId && (
          <Link to={"/post/" + prevPostId + "/"} rel="prev">
            ← {prevNode.title}
          </Link>
        )}
      </li>
      <li>
        {nextPostId && (
          <Link to={"/post/" + nextPostId + "/"} rel="next">
            {nextNode.title} →
          </Link>
        )}
      </li>
    </ul>
  )
}

function isRelated(thisTags, targetTags) {
  let related = false
  thisTags.forEach(thisTag => {
    targetTags.forEach(targetTag => {
      if (thisTag === targetTag) {
        related = true
      }
    })
  })
  return related
}

function RelatedPosts({ nodes, node }) {
  const thisTags = node.tag

  return (
    <div>
      <h2>Related posts</h2>
      <div
        style={{
          display: "grid",
          gridTemplateColumns: "repeat(auto-fit, minmax(250px, 1fr))",
          gridColumnGap: "20px",
          gridRowGap: "20px",
        }}
      >
        {nodes.map(targetNode => {
          const targetTags = targetNode.tag
          return (
            targetNode.contentful_id !== node.contentful_id &&
            isRelated(thisTags, targetTags) && <PostLink node={targetNode} />
          )
        })}
      </div>
    </div>
  )
}

function Post(props) {
  const postId = props.pageContext.contentful_id
  const prevPostId = props.pageContext.prevPostId
  const nextPostId = props.pageContext.nextPostId

  const data = useStaticQuery(
    graphql`
      query {
        posts: allContentfulPost(sort: { fields: date, order: DESC }) {
          nodes {
            id
            contentful_id
            images {
              gatsbyImageData(width: 1000)
            }
            videos {
              file {
                url
              }
            }
            title
            date(formatString: "yyyy / M")
            description {
              description
            }
            url
            thumb {
              id
              gatsbyImageData(width: 500, height: 300)
            }
            tag
          }
        }
      }
    `
  )

  const node = data.posts.nodes.find(n => n.contentful_id === postId)
  if (!node) return null

  return (
    <div
      style={{
        margin: "0 10%",
      }}
    >
      <div
        style={{
          maxWidth: "800px",
          margin: "0 auto",
        }}
      >
        <Header />
        <div style={{ marginTop: "4rem", position: "relative" }}>
          <PostContent node={node} />
          <Nav
            nodes={data.posts.nodes}
            prevPostId={prevPostId}
            nextPostId={nextPostId}
          />
          <div style={{ height: "4rem" }}></div>

          <RelatedPosts nodes={data.posts.nodes} node={node} />
        </div>
      </div>
      <Footer />
    </div>
  )
}

export default Post
